import { useEffect } from "react";
import { useRouter } from "next/router";
import Cookies from "js-cookie";

export default function HomePage() {
  const router = useRouter();

  useEffect(() => {
    const id = Cookies.get("usuario_id");
    if (!id) router.push("/login"); // redirige si no est√° logueado
  }, [router]);

  return (
    <div style={{ padding: "2rem" }}>
      <h1>Bienvenido al Home üè†</h1>
      <p>Has iniciado sesi√≥n correctamente.</p>
      <button
        onClick={() => {
          Cookies.remove("usuario_id");
          Cookies.remove("usuario_correo");
          router.push("/login");
        }}
        style={{
          background: "red",
          color: "white",
          padding: "8px 16px",
          border: "none",
          borderRadius: "6px",
          cursor: "pointer",
        }}
      >
        Cerrar sesi√≥n
      </button>
    </div>
  );
}
